export default "<b>11,571 കോടിയുടെ പദ്ധതികൾ</b><br><br>കാസർകോട് ജില്ല<br>∙ പേരോൽ - തളിപ്പറമ്പ്: 40.11 കിലോമീറ്റർ <b>3041.65 കോടി രൂപ</b><br>∙ തലപ്പാടി - ചെങ്കള: 39 കിലോമീറ്റർ <b>1981.07 കോടി</b><br>∙ ചെങ്കള - നീലേശ്വരം: 37.27 കിലോമീറ്റർ <b>1746.45 കോടി</b><br><br>കണ്ണൂർ ജില്ല<br>∙ തളിപ്പറമ്പ് - മുഴപ്പിലങ്ങാട്: 29.95 കിലോമീറ്റർ <b>2714.6 കോടി</b><br><br>കോഴിക്കോട് ജില്ല<br>∙ ബൈപാസ് - 28.4 കിലോമീറ്റർ <b>1853.42 കോടി</b><br>∙ പാലോളിപ്പാലം - മൂരാട് പാലം: 2.1 കിലോമീറ്റർ ആറുവരിപ്പാത <b>210.21 കോടി</b><br><br>ഇടുക്കി ജില്ല<br>∙ ചെറുതോണിപ്പുഴയിൽ പാലം <b>23.83 കോടി</b><br><br>- മനോരമ<br><br><a href=\https://www.manoramaonline.com/news/kerala/2020/10/10/central-govt-news-roads-in-kerala.html\>https://www.manoramaonline.com/news/kerala/2020/10/10/central-govt-news-roads-in-kerala.html</a>"
